@import url("https://fonts.googleapis.com/css2?family=Readex+Pro:wght@200;300;400;500;600;700&display=swap");
.homeContainer {
  display: flex;
  /* height: 100vh; */
  flex-direction: column;
  font-family: "Readex Pro", sans-serif;
  background-color: #2c2a2a86;
}

.hero {
  height: 80vh;
  /* background-image: url("https://i.gifer.com/JdHm.gif");
    justify-content: center; */
  align-items: center;
  text-align: center;
  color: #1d1034;
  display: flex;
}

@media only screen and (max-width: 992px) {
    .hero {
      height: auto;
    flex-direction: column;
    }
  }

.svgcont {
  flex: 1;

  border-radius: 12px;
  width: auto;
  margin: 50px;
}

.intro {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 56vh;
  background-image: url("https://i.ibb.co/XSL0b4m/IMG-20220805-174637-209.png"); 
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  /* background: yellow; */
}

@media only screen and (max-width: 992px) {
    .intro  {
      height: 14vh;
      background-size: cover;
    }
  }

.wolf {
  margin-top: 60px;
  height: 340px;
  width: 340px;
}

.container {
  text-align: center;
  font-size: 35px;
  padding: 6px;
  font-weight: 600;
  background-image: -webkit-linear-gradient(
    319deg,
    #6e4ceb 0%,
    #b641f0 37%,
    #b641f0 100%
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.heroimg {
  flex: 1;
  margin-right: 60px;
  height: 550px;
  width: 550px;
  border-radius: 12px;
}

@media only screen and (max-width: 992px) {
    .heroimg {
        margin-right: 0px;
        height: 100%;
        width: 100%;
      }
  }

.herotext {
  flex: 1;
  margin-right: 60px;
  border-radius: 12px;
  height: 450px;
  width: auto;
}

@media only screen and (max-width: 992px) {
    .herotext {
        margin-right: 0px;
        margin: 30px;
      }
  }

.button {
  display: flex;
  height: 50px;
  width: 180px;
  align-items: center;
  justify-content: center;
  border-radius: 15px;
  background: linear-gradient(319deg, #663dff 0%, #aa00ff 37%, #cc4499 100%);
  text-decoration: none;
}

.button2 {
  display: flex;
  height: 50px;
  width: 180px;
  color: #fff;
  align-items: center;
  justify-content: center;
  border-radius: 15px;
  background: linear-gradient(319deg, #fa000c 0%, #fa000c 37%, #fa000c 100%);
  color: #fff;
  text-decoration: none;
  font-size: 22px;
  font-weight: 500;
}

.button3 {
  display: flex;
  margin-right: 10px;
  text-align: center;
  justify-content: center;
  font: 2.5rem/4rem var(--font1);
  text-decoration: none;
  height: 80px;
  width: 100px;
  color: #fff;
  align-items: center;
  justify-content: center;
  border-radius: 20px;
  background: linear-gradient(to bottom, #fa0015be 0%, #fa0015be 100%);
  color: #fff;
  text-decoration: none;
  font-size: 30px;
  font-weight: 500;
}
@media only screen and (max-width: 992px) {
  .button3 {
      margin-top: 15px;

    }
}

